"use client";

import { useEffect, useState } from "react";
import Sidebar from "../components/dashboard/Sidebar";
import VitalsCard from "../components/dashboard/VitalsCard";
import HeartRateChart from "../components/dashboard/HeartRateChart";
import Alerts from "../components/dashboard/Alerts";
import Appointments from "../components/dashboard/Appointments";

export default function Dashboard() {
  const [bpmData, setBpmData] = useState<{ time: string; bpm: number }[]>([]);
  const [currentBpm, setCurrentBpm] = useState(79);

  useEffect(() => {
    const interval = setInterval(() => {
      const bpm = 60 + Math.floor(Math.random() * 40);
      setCurrentBpm(bpm);
      const now = new Date();
      setBpmData((prev) => [
        ...prev,
        { time: now.toLocaleTimeString(), bpm },
      ].slice(-10));
    }, 2000);

    return () => clearInterval(interval);
  }, []);

  return (
    <div className="flex">
      <Sidebar />
      <main className="flex-1 bg-gray-50 p-6 space-y-6">
        {/* Top cards */}
        <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
          <VitalsCard label="Heart Rate" value={currentBpm} unit="bpm" />
          <VitalsCard label="Blood Pressure" value="120/80" unit="mmHg" />
          <VitalsCard label="Body Temperature" value="98.6" unit="Â°F" />
          <VitalsCard label="Oxygen Saturation" value="98" unit="%" />
        </div>

        <HeartRateChart data={bpmData} />
        <Alerts />
        <Appointments />
      </main>
    </div>
  );
}
